/**
 * Estos métodos nos ayudan a crear acciones que ejectuar de una manera
 * más sencilla
 */

// Comenzamos una nueva búsqueda
export const startSearch = search => {
  return {
    type: 'SEARCH_START',
    search
  }
}

/* Retornamos los resultados
export const successSearch = results => {
  return {
    type: 'SEARCH_SUCCESS',
    results

  }
}*/

// Retornamos los resultados
export const searchRepositories = search => {
  return {	
    type: 'SEARCH',
    payload: fetch(`https://api.github.com/search/repositories?q=${ search }`)
      .then(res => {
        return res.json();
      })
      .then(json => {
       return json.items;
      })    
  }
}


